@import '../../styles/variables.css';

.projects-page {
  min-height: 100vh;
  padding: 0 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  overflow: hidden;
  background: transparent;
}


/* Grain/noise texture - disabled to avoid visible boundary */

.projects-network-container {
  width: 100%;
  max-width: 1400px;
  height: 80vh;
  position: relative;
  margin: 0 auto;
  z-index: 2;
}

/* SVG Container for connection lines */
.connections-svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  pointer-events: none;
}

.connection-line {
  stroke: rgba(255, 255, 255, 0.25);
  stroke-width: 2.5;
  fill: none;
  opacity: 0.7;
  stroke-dasharray: none;
  transition: all 0.3s ease;
}

/* Interactive lines - Enhanced when hovering connected nodes */
.connection-line.active {
  stroke-width: 3.5;
  opacity: 0.75;
  transition: all 0.3s ease;
}

.connection-line.active[data-from="signtalker"],
.connection-line.active[data-to="signtalker"] {
  stroke: var(--accent-sage);
}

.connection-line.active[data-from="ellenengineer"],
.connection-line.active[data-to="ellenengineer"] {
  stroke: var(--text-medium);
}

.connection-line.active[data-from="nordpunkt"],
.connection-line.active[data-to="nordpunkt"] {
  stroke: var(--accent-terracotta);
}


@keyframes dash {
  to {
    stroke-dashoffset: -100;
  }
}


/* Nodes Container */
.nodes-container {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.project-node {
  position: absolute;
  transform: translate(-50%, -50%);
  cursor: pointer;
  animation: float 6s ease-in-out infinite;
  transition: transform var(--transition-normal);
}

.project-node:nth-child(1) {
  animation: float 6s ease-in-out infinite;
}

.project-node:nth-child(2) {
  animation: float 7s ease-in-out infinite;
}

.project-node:nth-child(3) {
  animation: float 8s ease-in-out infinite;
}

@keyframes float {
  0%, 100% {
    transform: translate(-50%, -50%) translateY(0px) translateX(0px);
  }
  25% {
    transform: translate(-50%, -50%) translateY(-10px) translateX(5px);
  }
  50% {
    transform: translate(-50%, -50%) translateY(-5px) translateX(-5px);
  }
  75% {
    transform: translate(-50%, -50%) translateY(-15px) translateX(3px);
  }
}

.project-node:hover {
  transform: translate(-50%, -50%) scale(1.05);
  z-index: 10;
}

.node-circle {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  background: rgba(28, 25, 23, 0.92);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  box-shadow:
    var(--shadow-md),
    0 0 0 1px rgba(255, 255, 255, 0.06);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all var(--transition-normal);
  position: relative;
}

.project-node:nth-child(1) .node-circle {
  border-color: rgba(var(--accent-sage-rgb), 0.45);
  box-shadow:
    var(--shadow-md),
    0 0 0 1px rgba(255, 255, 255, 0.06),
    0 0 15px rgba(var(--accent-sage-rgb), 0.1);
}

.project-node:nth-child(2) .node-circle {
  border-color: rgba(var(--accent-sand-rgb), 0.45);
  box-shadow:
    var(--shadow-md),
    0 0 0 1px rgba(255, 255, 255, 0.06),
    0 0 15px rgba(var(--accent-sand-rgb), 0.1);
}

.project-node:nth-child(3) .node-circle {
  border-color: rgba(var(--accent-terracotta-rgb), 0.45);
  box-shadow:
    var(--shadow-md),
    0 0 0 1px rgba(255, 255, 255, 0.06),
    0 0 15px rgba(var(--accent-terracotta-rgb), 0.1);
}

.project-node:nth-child(1):hover .node-circle {
  border-color: rgba(var(--accent-sage-rgb), 0.7);
  box-shadow:
    var(--shadow-lg),
    0 0 25px rgba(var(--accent-sage-rgb), 0.25),
    0 0 50px rgba(var(--accent-sage-rgb), 0.1);
}

.project-node:nth-child(2):hover .node-circle {
  border-color: rgba(var(--accent-sand-rgb), 0.7);
  box-shadow:
    var(--shadow-lg),
    0 0 25px rgba(var(--accent-sand-rgb), 0.25),
    0 0 50px rgba(var(--accent-sand-rgb), 0.1);
}

.project-node:nth-child(3):hover .node-circle {
  border-color: rgba(var(--accent-terracotta-rgb), 0.7);
  box-shadow:
    var(--shadow-lg),
    0 0 25px rgba(var(--accent-terracotta-rgb), 0.25),
    0 0 50px rgba(var(--accent-terracotta-rgb), 0.1);
}



.node-inner {
  text-align: center;
  padding: 1rem;
}

.node-name {
  font-size: 1.1rem;
  font-weight: 500;
  color: var(--text-dark);
  margin-bottom: 0.25rem;
  line-height: 1.3;
  font-family: 'Menlo', 'Monaco', 'Courier New', monospace;
  letter-spacing: 0.3px;
}

.node-tag {
  font-family: 'Menlo', 'Monaco', 'Courier New', monospace;
  font-size: 0.7rem;
  color: var(--text-medium);
  letter-spacing: 1.5px;
  text-transform: uppercase;
  margin-bottom: 0.15rem;
}

.node-year {
  font-size: 0.65rem;
  color: var(--text-light);
  font-weight: 400;
  font-family: 'Menlo', 'Monaco', 'Courier New', monospace;
  letter-spacing: 1px;
}

/* Orbiting Particles */
.particle-orbit {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.particle {
  position: absolute;
  top: 50%;
  left: 50%;
  width: var(--size, 6px);
  height: var(--size, 6px);
  border-radius: 50%;
  opacity: var(--opacity, 0.6);
  animation: particleOrbit var(--duration, 8s) linear infinite;
  animation-delay: var(--delay, 0s);
  animation-direction: var(--anim-direction, normal);
}

.project-node:nth-child(1) .particle {
  background: var(--accent-sage);
  box-shadow:
    0 0 4px rgba(var(--accent-sage-rgb), 0.6),
    0 0 8px rgba(var(--accent-sage-rgb), 0.4),
    0 0 12px rgba(var(--accent-sage-rgb), 0.2);
}

.project-node:nth-child(2) .particle {
  background: var(--accent-sand);
  box-shadow:
    0 0 4px rgba(var(--accent-sand-rgb), 0.6),
    0 0 8px rgba(var(--accent-sand-rgb), 0.4),
    0 0 12px rgba(var(--accent-sand-rgb), 0.2);
}

.project-node:nth-child(3) .particle {
  background: var(--accent-terracotta);
  box-shadow:
    0 0 4px rgba(var(--accent-terracotta-rgb), 0.6),
    0 0 8px rgba(var(--accent-terracotta-rgb), 0.4),
    0 0 12px rgba(var(--accent-terracotta-rgb), 0.2);
}

@keyframes particleOrbit {
  0% {
    transform: translate(-50%, -50%) rotate(var(--start-angle, 0deg)) translateX(var(--radius, 110px)) rotate(calc(-1 * var(--start-angle, 0deg)));
  }
  100% {
    transform: translate(-50%, -50%) rotate(calc(var(--start-angle, 0deg) + calc(360deg * var(--direction, 1)))) translateX(var(--radius, 110px)) rotate(calc(-1 * var(--start-angle, 0deg) - calc(360deg * var(--direction, 1))));
  }
}

/* Responsive Design */
@media (max-width: 1024px) {
  .projects-network-container {
    height: 70vh;
  }

  .node-circle {
    width: 150px;
    height: 150px;
  }

  .node-name {
    font-size: 1.1rem;
  }

  .node-year {
    font-size: 0.9rem;
  }

}

@media (max-width: 768px) {
  .projects-page {
    padding: 2rem 1rem;
  }

  .projects-network-container {
    height: 60vh;
  }

  .node-circle {
    width: 160px;
    height: 160px;
  }

  .node-name {
    font-size: 0.95rem;
  }

  .node-year {
    font-size: 0.8rem;
  }
}

@media (max-width: 480px) {
  .projects-network-container {
    height: 80vh;
  }

  .node-circle {
    width: 125px;
    height: 125px;
  }

  .node-name {
    font-size: 0.75rem;
  }

  .node-year {
    font-size: 0.6rem;
  }

  .particle-orbit {
    transform: scale(0.65);
  }

  .connection-line {
    stroke-width: 1.5;
  }

  .connection-line.active {
    stroke-width: 2.5;
  }
}

/* Landscape mobile - 75% node size */
@media (max-height: 500px) and (orientation: landscape) {
  .projects-page {
    padding: 0.5rem 1rem;
  }

  .projects-network-container {
    height: 70vh;
    padding-top: 20px;
  }

  .node-circle {
    width: 120px;
    height: 120px;
  }

  .node-inner {
    padding: 0.5rem;
  }

  .node-name {
    font-size: 0.7rem;
  }

  .node-tag {
    font-size: 0.5rem;
  }

  .node-year {
    font-size: 0.5rem;
  }

  .particle-orbit {
    transform: scale(0.6);
  }

  .connection-line {
    stroke-width: 1.5;
  }
}
