/* ===== PAGE ===== */
.about-page {
  background: transparent;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}

.about-page::before {
  content: '';
  position: absolute;
  inset: 0;
  background-image:
    radial-gradient(circle, rgba(var(--accent-rose-warm-rgb), 0.04) 1px, transparent 1px);
  background-size: 24px 24px;
  pointer-events: none;
}

/* ===== AMBIENT PARTICLES ===== */
.ambient-particles {
  position: absolute;
  inset: 0;
  pointer-events: none;
  z-index: 1;
  overflow: hidden;
}

.ambient-particle {
  position: absolute;
  border-radius: 50%;
  background: rgba(var(--accent-rose-warm-rgb), 0.4);
  box-shadow: 0 0 6px rgba(var(--accent-rose-warm-rgb), 0.3);
  animation: particleFloat linear infinite;
  opacity: 0;
}

@keyframes particleFloat {
  0%   { opacity: 0; transform: translateY(0) translateX(0); }
  15%  { opacity: 0.7; }
  85%  { opacity: 0.7; }
  100% { opacity: 0; transform: translateY(-120px) translateX(var(--drift)); }
}

/* ===== MAIN CONTENT ===== */
.about-main {
  flex: 1;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 60px 80px 40px;
  position: relative;
  z-index: 10;
}

/* ===== CARD LAYOUT ===== */
.card-layout {
  width: 100%;
  max-width: 860px;
}

.card-layout.has-image {
  display: flex;
  align-items: flex-start;
  gap: 48px;
}

.card-text {
  flex: 1;
  min-width: 0;
}

.card-image {
  flex: 0 0 280px;
  width: 280px;
  max-height: 340px;
  position: relative;
}

/* ===== CARD TEXT ===== */
.card-eyebrow {
  display: flex;
  align-items: center;
  gap: 14px;
  margin-bottom: 14px;
}

.card-chip {
  font-family: 'SF Mono', 'Monaco', 'Courier New', monospace;
  font-size: 11px;
  font-weight: 600;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  color: var(--background-beige);
  background: rgba(var(--accent-rose-warm-rgb), 0.8);
  padding: 4px 12px;
  border-radius: 2px;
}

.card-loc {
  font-family: 'SF Mono', 'Monaco', 'Courier New', monospace;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 1px;
  color: rgba(var(--accent-sand-rgb), 0.5);
  text-transform: uppercase;
}

.card-year {
  font-family: 'SF Mono', 'Monaco', 'Courier New', monospace;
  font-size: 64px;
  font-weight: 700;
  color: rgba(var(--accent-rose-warm-rgb), 0.9);
  margin: 0 0 20px;
  line-height: 1;
  letter-spacing: -2px;
  text-shadow: 0 0 16px rgba(var(--accent-rose-warm-rgb), 0.18);
}

.card-desc {
  font-family: 'SF Mono', 'Monaco', 'Courier New', monospace;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.8;
  color: var(--text-medium);
  margin: 0;
  max-width: 540px;
  min-height: 120px;
}

.card-desc p {
  margin: 0 0 12px 0;
}

.card-desc p:last-child {
  margin-bottom: 0;
}

.card-prompt {
  color: rgba(var(--accent-rose-warm-rgb), 0.6);
}

/* ===== GLITCH IMAGE ===== */
.glitch-img {
  position: relative;
  overflow: hidden;
  border-radius: 4px;
  width: 100%;
  height: 100%;
  max-height: 340px;
  touch-action: pan-y;
}

.glitch-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center top;
  display: block;
  filter: grayscale(0.35) brightness(0.8) contrast(1.1);
  animation: glitchShift 8s steps(1) infinite;
}

/* Scanlines overlay */
.glitch-img::before {
  content: '';
  position: absolute;
  inset: 0;
  background: repeating-linear-gradient(
    to bottom,
    transparent 0px,
    transparent 2px,
    rgba(0, 0, 0, 0.15) 2px,
    rgba(0, 0, 0, 0.15) 4px
  );
  pointer-events: none;
  z-index: 3;
}

/* Green tint + vignette overlay */
.glitch-img::after {
  content: '';
  position: absolute;
  inset: 0;
  background:
    radial-gradient(ellipse at center, transparent 50%, rgba(0, 0, 0, 0.6) 100%),
    linear-gradient(to bottom, rgba(var(--accent-rose-warm-rgb), 0.06) 0%, transparent 100%);
  pointer-events: none;
  z-index: 4;
}

/* Periodic glitch burst */
@keyframes glitchShift {
  0%, 89%, 100% {
    transform: translate(0);
    filter: grayscale(0.35) brightness(0.8) contrast(1.1);
    clip-path: none;
  }
  90% {
    transform: translate(-4px, 1px);
    filter: grayscale(0.35) brightness(1.2) contrast(1.1) hue-rotate(80deg);
  }
  91% {
    transform: translate(3px, 0);
    clip-path: inset(15% 0 60% 0);
    filter: grayscale(0.35) brightness(0.8) contrast(1.1);
  }
  92% {
    transform: translate(-2px, -1px);
    clip-path: inset(65% 0 10% 0);
    filter: grayscale(0.35) brightness(0.9) contrast(1.1) hue-rotate(-40deg);
  }
  93% {
    transform: translate(1px, 2px);
    clip-path: inset(40% 0 30% 0);
  }
  94% {
    transform: translate(0);
    filter: grayscale(0.35) brightness(0.8) contrast(1.1);
    clip-path: none;
  }
}

/* Flickering noise band */
.card-image::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  height: 3px;
  background: rgba(var(--accent-rose-warm-rgb), 0.15);
  z-index: 5;
  pointer-events: none;
  animation: noiseBand 4s steps(1) infinite;
}

@keyframes noiseBand {
  0%, 100% { top: 20%; opacity: 0; }
  15%      { top: 20%; opacity: 0; }
  16%      { top: 45%; opacity: 1; }
  18%      { top: 47%; opacity: 0.6; }
  19%      { top: 47%; opacity: 0; }
  50%      { top: 70%; opacity: 0; }
  51%      { top: 15%; opacity: 0.8; }
  53%      { top: 17%; opacity: 0; }
  80%      { top: 80%; opacity: 0; }
  81%      { top: 65%; opacity: 1; }
  83%      { top: 67%; opacity: 0; }
}

/* ===== TIMELINE ===== */
.timeline {
  padding: 40px 60px 40px;
  position: relative;
  z-index: 10;
}

.timeline-track {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  position: relative;
  padding-top: 12px;
  cursor: pointer;
}

/* Base wire */
.timeline-line {
  position: absolute;
  top: 16px;
  left: 0;
  right: 0;
  height: 2px;
  background: rgba(var(--accent-rose-warm-rgb), 0.12);
  box-shadow: 0 0 6px rgba(var(--accent-rose-warm-rgb), 0.06);
  overflow: visible;
}

.timeline-line::after {
  content: '';
  position: absolute;
  inset: -3px 0;
  background: rgba(var(--accent-rose-warm-rgb), 0.04);
  filter: blur(4px);
  border-radius: 2px;
}

/* Spark particles */
.tl-spark {
  position: absolute;
  top: -1px;
  width: 6px;
  height: 4px;
  border-radius: 50%;
  background: rgba(var(--accent-rose-light-rgb), 0.9);
  box-shadow:
    0 0 4px rgba(var(--accent-rose-light-rgb), 0.8),
    0 0 10px rgba(var(--accent-rose-warm-rgb), 0.5);
  animation: sparkTravel linear infinite;
  opacity: 0;
  z-index: 3;
}

@keyframes sparkTravel {
  0%   { left: -6px; opacity: 0; }
  3%   { opacity: 1; }
  50%  { opacity: 0.7; }
  97%  { opacity: 1; }
  100% { left: 100%; opacity: 0; }
}

/* ===== ENERGY BLOB (travels between nodes) ===== */
.tl-energy-blob {
  position: absolute;
  top: 9px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: rgba(var(--accent-rose-light-rgb), 0.95);
  box-shadow:
    0 0 10px rgba(var(--accent-rose-warm-rgb), 0.9),
    0 0 28px rgba(var(--accent-rose-warm-rgb), 0.5),
    0 0 50px rgba(var(--accent-rose-warm-rgb), 0.2);
  transform: translateX(-50%);
  opacity: 0;
  z-index: 6;
  pointer-events: none;
}

/* ===== NODES ===== */
.tl-node {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 1;
  outline: none;
}

.tl-dot {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: rgba(var(--accent-rose-warm-rgb), 0.3);
  border: 2px solid rgba(var(--accent-rose-warm-rgb), 0.15);
  transition: background 0.3s ease, border-color 0.3s ease, box-shadow 0.3s ease, transform 0.3s ease;
  transform: scale(0.56);
}

.tl-node:hover .tl-dot {
  background: rgba(var(--accent-rose-warm-rgb), 0.6);
  border-color: rgba(var(--accent-rose-warm-rgb), 0.4);
  box-shadow: 0 0 12px rgba(var(--accent-rose-warm-rgb), 0.3);
}

.tl-node.active .tl-dot {
  background: rgba(var(--accent-rose-light-rgb), 0.95);
  border-color: rgba(var(--accent-rose-warm-rgb), 0.7);
  box-shadow:
    0 0 10px rgba(var(--accent-rose-warm-rgb), 0.6),
    0 0 24px rgba(var(--accent-rose-warm-rgb), 0.3);
  transform: scale(1);
  cursor: grab;
  touch-action: none;
  animation: dotCharge 2s ease-in-out infinite;
}

.tl-node.active .tl-dot.dragging {
  cursor: grabbing;
  transform: scale(1.25);
  box-shadow:
    0 0 16px rgba(var(--accent-rose-warm-rgb), 0.8),
    0 0 32px rgba(var(--accent-rose-warm-rgb), 0.4);
}

@keyframes dotCharge {
  0%, 100% { box-shadow: 0 0 10px rgba(var(--accent-rose-warm-rgb), 0.6), 0 0 24px rgba(var(--accent-rose-warm-rgb), 0.3); }
  50%      { box-shadow: 0 0 14px rgba(var(--accent-rose-warm-rgb), 0.8), 0 0 30px rgba(var(--accent-rose-warm-rgb), 0.4); }
}

.tl-label {
  font-family: 'SF Mono', 'Monaco', 'Courier New', monospace;
  font-size: 13px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.45);
  transition: color 0.3s ease;
}

.tl-node:hover .tl-label {
  color: rgba(255, 255, 255, 0.65);
}

.tl-node.active .tl-label {
  color: rgba(var(--accent-rose-warm-rgb), 0.9);
}

.tl-sublabel {
  font-family: 'SF Mono', 'Monaco', 'Courier New', monospace;
  font-size: 10px;
  letter-spacing: 0.5px;
  color: rgba(255, 255, 255, 0.30);
  transition: color 0.3s ease;
}

.tl-node:hover .tl-sublabel {
  color: rgba(255, 255, 255, 0.45);
}

.tl-node.active .tl-sublabel {
  color: rgba(var(--accent-rose-warm-rgb), 0.6);
}

/* ===== TIMELINE HINT ===== */
.timeline-hint {
  text-align: center;
  margin-top: 16px;
  font-family: 'SF Mono', 'Monaco', 'Courier New', monospace;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.25);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
}

.timeline-hint-key {
  background: rgba(255, 255, 255, 0.06);
  color: rgba(255, 255, 255, 0.4);
  padding: 2px 8px;
  border-radius: 4px;
  font-weight: 600;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.3);
}

/* ===== RESPONSIVE ===== */

/* Intermediate - tighter first, then smaller */
@media (max-width: 1200px) {
  .about-main {
    padding: 50px 60px 35px;
  }

  .card-layout.has-image {
    gap: 40px;
  }

  .card-image {
    flex: 0 0 250px;
    width: 250px;
    max-height: 310px;
  }

  .glitch-img {
    max-height: 310px;
  }

  .card-year {
    font-size: 56px;
  }
}

@media (max-width: 1050px) {
  .about-main {
    padding: 40px 40px 30px;
  }

  .card-layout.has-image {
    gap: 32px;
  }

  .card-image {
    flex: 0 0 220px;
    width: 220px;
    max-height: 280px;
  }

  .glitch-img {
    max-height: 280px;
  }

  .card-year {
    font-size: 48px;
    margin-bottom: 16px;
  }

  .card-desc {
    font-size: 13px;
    line-height: 1.7;
  }

  .card-chip {
    font-size: 10px;
    padding: 3px 10px;
  }

  .card-loc {
    font-size: 11px;
  }
}

@media (max-width: 900px) {
  .about-main {
    padding: 30px 25px 25px;
  }

  .card-layout.has-image {
    gap: 24px;
  }

  .card-image {
    flex: 0 0 180px;
    width: 180px;
    max-height: 240px;
  }

  .glitch-img {
    max-height: 240px;
  }

  .card-year {
    font-size: 40px;
    margin-bottom: 12px;
  }

  .card-desc {
    font-size: 12px;
    min-height: 100px;
  }

  .card-chip {
    font-size: 9px;
    padding: 3px 8px;
  }

  .card-loc {
    font-size: 10px;
  }

  .card-eyebrow {
    gap: 10px;
    margin-bottom: 10px;
  }
}

@media (max-width: 820px) {
  .about-main {
    padding: 20px 16px 20px;
  }

  .card-layout.has-image {
    gap: 16px;
  }

  .card-image {
    flex: 0 0 150px;
    width: 150px;
    max-height: 200px;
  }

  .glitch-img {
    max-height: 200px;
  }

  .card-year {
    font-size: 34px;
    margin-bottom: 10px;
  }

  .card-desc {
    font-size: 11px;
    min-height: 80px;
  }
}

@media (max-width: 768px) {
  .about-page {
    min-height: calc(100dvh - 72px);
    justify-content: space-between;
  }

  .about-main {
    padding: 0 12px 0;
    flex: 1;
  }

  .card-layout {
    max-width: 100%;
  }

  .card-layout.has-image {
    flex-direction: column;
    gap: 16px;
  }

  .card-image {
    flex: none;
    width: 100%;
    max-height: 180px;
    display: flex;
    justify-content: center;
  }

  .glitch-img {
    width: auto;
    height: auto;
    max-height: 180px;
  }

  .glitch-img img {
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 180px;
    object-fit: contain;
  }

  .card-eyebrow {
    gap: 8px;
    margin-bottom: 8px;
  }

  .card-chip {
    font-size: 9px;
    padding: 3px 8px;
  }

  .card-loc {
    font-size: 10px;
  }

  .card-year {
    font-size: 36px;
    margin-bottom: 12px;
    text-shadow:
      0 0 10px rgba(var(--accent-rose-warm-rgb), 0.6),
      0 0 20px rgba(var(--accent-rose-warm-rgb), 0.4),
      0 0 30px rgba(var(--accent-rose-warm-rgb), 0.2);
  }

  .card-desc {
    font-size: 12px;
    line-height: 1.6;
    min-height: auto;
  }

  .tl-node.active .tl-dot {
    transform: scale(1.2);
  }

  .timeline {
    padding: 0 8px 20px;
    margin-top: -20px;
  }

  .tl-label {
    font-size: 10px;
  }

  .tl-sublabel {
    display: none;
  }

  .timeline-hint {
    display: none;
  }
}

/* Landscape mobile - smaller text */
@media (max-height: 500px) and (orientation: landscape) {
  .about-page {
    height: 100vh;
    min-height: auto;
    display: flex;
    flex-direction: column;
  }

  .about-main {
    flex: 1;
    padding: 10px 3rem;
    overflow: hidden;
  }

  .card-chip {
    font-size: 8px;
    padding: 2px 6px;
  }

  .card-loc {
    font-size: 8px;
  }

  .card-year {
    font-size: 28px;
    margin-bottom: 8px;
    text-shadow:
      0 0 10px rgba(var(--accent-rose-warm-rgb), 0.6),
      0 0 20px rgba(var(--accent-rose-warm-rgb), 0.4),
      0 0 30px rgba(var(--accent-rose-warm-rgb), 0.2);
  }

  .card-desc {
    font-size: 11px;
    line-height: 1.5;
  }

  .card-image {
    flex: 0 0 auto;
    width: auto;
    max-width: 280px;
    max-height: 180px;
  }

  .glitch-img {
    max-height: 180px;
  }

  .glitch-img img {
    width: auto;
    height: auto;
    max-width: 280px;
    max-height: 180px;
    object-fit: contain;
  }

  .timeline {
    padding: 10px 40px 15px;
    flex-shrink: 0;
  }

  .tl-label {
    font-size: 9px;
  }

  .tl-sublabel {
    font-size: 8px;
  }

  .timeline-hint {
    display: none;
  }
}
